<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'order' }">
            Оформление заказа
          </router-link>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info"> {{ products.length }} товара </span>
    </div>

    <section class="cart">
      <form
        class="cart__form form"
        action="#"
        method="POST"
        @submit.prevent="order"
      >
        <div class="cart__field">
          <div class="cart__data">
            <base-form-text
              title="ФИО"
              :error="formError.name"
              v-model="formData.name"
              placeholder="Введите ваше полное имя"
            />

            <base-form-text
              title="Адрес доставки"
              :error="formError.address"
              v-model="formData.address"
              placeholder="Введите ваш адрес"
            />

            <base-form-text
              title="Телефон"
              :error="formError.phone"
              v-model="formData.phone"
              placeholder="Введите ваш телефон"
            />

            <base-form-text
              title="Email"
              :error="formError.email"
              v-model="formData.email"
              placeholder="Введите ваш Email"
            />

            <base-form-textarea
              title="Комментарий к заказу"
              :error="formError.comment"
              v-model="formData.comment"
              placeholder="Ваши пожелания"
            />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <base-form-radio v-model="formData.delivery" value="0">
                <span class="options__value"> Самовывоз <b>бесплатно</b> </span>
              </base-form-radio>

              <base-form-radio v-model="formData.delivery" value="500">
                <span class="options__value"> Курьером <b>500 ₽</b> </span>
              </base-form-radio>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <base-form-radio v-model="formData.pay" value="card">
                <span class="options__value"> Картой при получении </span>
              </base-form-radio>

              <base-form-radio v-model="formData.pay" value="cash">
                <span class="options__value"> Наличными при получении </span>
              </base-form-radio>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li
              class="cart__order"
              v-for="product in products"
              :key="product.id"
            >
              <h3>{{ product.product.title }}</h3>
              <b
                >{{ formatPrice(product.product.price) }} ₽ x
                {{ product.amount }} шт</b
              >
              <span>Артикул: {{ product.productId }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>
              Доставка: <b>{{ formData.delivery }} ₽</b>
            </p>
            <p>
              Итого: <b>{{ products.length }}</b> товара на сумму
              <b>{{ formatPrice(totalPrice) }} ₽</b>
            </p>
          </div>

          <button
            class="cart__button button button--primery"
            type="submit"
            :disabled="formPostLoading"
          >
            Оформить заказ
          </button>
          <div v-show="formPostLoading">Отправляем заказ...</div>
        </div>

        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from "@/components/BaseFormText.vue";
import BaseFormTextarea from "@/components/BaseFormTextarea.vue";
import BaseFormRadio from "@/components/BaseFormRadio.vue";
import { mapGetters } from "vuex";
import numberFormat from "@/helpers/numberFormat";
import axios from "axios";
import API_BASE_URL from "@/config";

export default {
  data() {
    return {
      formData: {
        pay: "card",
        delivery: 0,
      },
      formError: {},
      formErrorMessage: "",
      formPostLoading: false,
    };
  },
  components: { BaseFormText, BaseFormTextarea, BaseFormRadio },
  computed: {
    ...mapGetters({
      products: "cartDetailProducts",
      totalPrice: "cartTotalPrice",
    }),
  },
  methods: {
    formatPrice(value) {
      return numberFormat(value);
    },
    order() {
      this.formError = {};
      this.formErrorMessage = "";
      this.formPostLoading = true;
      axios
        .post(
          `${API_BASE_URL}/api/orders`,
          {
            name: this.formData.name,
            address: this.formData.address,
            phone: this.formData.phone,
            email: this.formData.email,
            comment: this.formData.comment,
          },
          {
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            },
          }
        )
        .then((response) => {
          this.formPostLoading = false;
          this.$store.commit("resetCart");
          this.$store.commit("updateOrderInfo", response.data);
          this.$router.push({
            name: "orderInfo",
            params: { id: response.data.id },
          });
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
          this.formPostLoading = false;
        });
    },
  },
};
</script>
