<template>
  <li class="catalog__item">
    <router-link
      class="catalog__pic"
      href="#"
      :to="{name: 'product', params: {id: product.id}}"
    >
      <img :src="product.image" :alt="product.title" />
    </router-link>

    <h3 class="catalog__title">
      <a href="#" v-if="product">{{ product.title }}</a>
    </h3>

    <span class="catalog__price" v-if="product">{{ formatProductPrice }} ₽ </span>

    <ul class="colors colors--black">
      <li v-for="color in product.colors" :key="color.id">
        <label
          :for="'item' + product.id + 'color' + color.id"
          class="colors__label"
        >
          <input
            :id="'item' + product.id + 'color' + color.id"
            class="colors__radio sr-only"
            type="radio"
            :value="color.id"
            v-model="selectedColorId"
          />
          <span
            class="colors__value"
            :style="{
              backgroundColor: color.code,
            }"
          >
          </span>
        </label>
      </li>
    </ul>
  </li>
</template>

<script>
import gotoPage from "@/helpers/gotoPage";
import numberFormat from '@/helpers/numberFormat';

export default {
  data() {
    return {
      selectedColorId: 0,
    };
  },
  props: ["product"],
  computed: {
    formatProductPrice() {
      return numberFormat(this.product.price);
    },
  },
  methods: {
    gotoPage,
  },
};
</script>
