<template>
  <li class="options__item">
    <label class="options__label">
      <input
        class="options__radio sr-only"
        type="radio"
        :value="value"
        v-model="dataValue"
      />
      <slot/>
    </label>
  </li>
</template>

<script>
export default {
  props: ['value', 'modelValue'],
  computed: {
    dataValue: {
      get() {
        return this.modelValue;
      },
      set(value) {
        this.$emit("update:modelValue", value);
      },
    },
  }
};
</script>
