import { createStore } from 'vuex';
import axios from 'axios';
import API_BASE_URL from '@/config';

const store = createStore({
  state: {
    userAccessKey: null,
    cartProductsData: [],
    cartProductsLoading: false,
    cartProducts: [],
    orderInfo: null,
  },
  mutations: {
    updateOrderInfo(state, { orderInfo }) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProductsData = [];
      state.cartProducts = [];
    },
    updateCartProductAmount(state, { productId, amount }) {
      const item = state.cartProducts.find((product) => product.productId === productId);
      if (item) {
        item.amount = amount;
      }
    },
    updateUserAccessKey(state, { accessKey }) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, { productsData }) {
      state.cartProductsData = productsData;
    },
    updateCartProductsLoading(state, { loading }) {
      state.cartProductsLoading = loading;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((p) => {
        return {
          productId: p.product.id,
          amount: p.quantity,
        };
      });
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map((item) => {
        const productData = state.cartProductsData.find((p) => p.product.id === item.productId).product;

        return {
          ...item,
          product: {
            ...productData,
            image: productData.image.file.url,
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce((sum, item) => sum + item.amount * item.product.price, 0);
    },
  },
  actions: {
    loadOrderInfo(context, { orderId }) {
      return axios.get(`${API_BASE_URL}/api/orders/${orderId}`, {
        params: {
          userAccessKey: context.state.userAccessKey,
        }
      })
        .then((response) => {
          context.commit('updateOrderInfo', {
            orderInfo: response.data,
          });
        });
    },
    loadCart(context) {
      context.commit('updateCartProductsLoading', {
        loading: true,
      });
      return axios.get(`${API_BASE_URL}/api/baskets`, {
        params: {
          userAccessKey: context.state.userAccessKey,
        }
      })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', {
              accessKey: response.data.user.accessKey,
            });
          }
          context.commit('updateCartProductsData', {
            productsData: response.data.items,
          });
          context.commit('syncCartProducts');
        })
        .then(() => {
          context.commit('updateCartProductsLoading', {
            loading: false,
          });
        });
    },
    addProductToCart(context, { productId, amount }) {
      return axios.post(`${API_BASE_URL}/api/baskets/products`, {
        productId: productId,
        quantity: amount,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        }
      })
        .then((response) => {
          context.commit('updateCartProductsData', {
            productsData: response.data.items,
          });
          context.commit('syncCartProducts');
        });
    },
    updateCartProductAmount(context, { productId, amount }) {
      context.commit('updateCartProductAmount', {
        productId,
        amount,
      });

      if (amount < 1) {
        return null;
      }

      return axios.put(`${API_BASE_URL}/api/baskets/products`, {
        productId: productId,
        quantity: amount,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        }
      })
        .then((response) => {
          context.commit('updateCartProductsData', {
            productsData: response.data.items,
          });
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
    deleteCartProductAmount(context, { productId }) {
      return axios.delete(`${API_BASE_URL}/api/baskets/products`, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
        data: {
          productId,
        }
      })
        .then((response) => {
          context.commit('updateCartProductsData', {
            productsData: response.data.items,
          });
          context.commit('syncCartProducts');
        });
    },

  },
});

export default store;
